import { f as fail } from "../../../../../chunks/index.js";
import { a as validateResetToken, r as resetPassword } from "../../../../../chunks/auth.js";
import { S as SecurityManager } from "../../../../../chunks/security.js";
import { p as passwordResetSchema } from "../../../../../chunks/validation.js";
const load = async ({ params }) => {
  const { token } = params;
  const user = await validateResetToken(token);
  if (!user) {
    return {
      token: null,
      error: "This reset link is invalid or has expired. Please request a new one."
    };
  }
  return {
    token,
    email: user.email
  };
};
const actions = {
  default: async ({ request, getClientAddress }) => {
    const security = SecurityManager.getInstance();
    const clientIP = getClientAddress();
    try {
      const formData = await request.formData();
      const token = formData.get("token");
      const password = formData.get("password");
      const confirmPassword = formData.get("confirmPassword");
      const validationResult = passwordResetSchema.safeParse({
        token,
        password,
        confirmPassword
      });
      if (!validationResult.success) {
        const errors = {};
        validationResult.error.issues.forEach((issue) => {
          const key = issue.path[0];
          errors[key] = issue.message;
        });
        return fail(400, {
          errors,
          token
        });
      }
      const user = await resetPassword(token, password);
      await security.logEvent("PASSWORD_RESET_COMPLETED", clientIP, {
        userId: user.id,
        email: user.email
      });
      return {
        success: true,
        message: "Password reset successfully"
      };
    } catch (error) {
      console.error("Password reset error:", error);
      await security.logEvent("PASSWORD_RESET_ERROR", clientIP, {
        error: error instanceof Error ? error.message : "Unknown error"
      });
      return fail(500, {
        errors: {
          general: "An error occurred while resetting your password. Please try again."
        }
      });
    }
  }
};
export {
  actions,
  load
};
