import { z } from "zod";
const passwordPatterns = {
  uppercase: /[A-Z]/,
  lowercase: /[a-z]/,
  number: /[0-9]/,
  special: /[^A-Za-z0-9]/
};
const errorMessages = {
  email: "Please enter a valid email address",
  password: {
    minLength: "Password must be at least 8 characters long",
    uppercase: "Password must contain at least one uppercase letter",
    lowercase: "Password must contain at least one lowercase letter",
    number: "Password must contain at least one number",
    special: "Password must contain at least one special character"
  },
  orgName: "Organization name must be between 2 and 100 characters",
  name: "Name must be between 1 and 50 characters"
};
const registerSchema = z.object({
  firstName: z.string().min(1, errorMessages.name).max(50, errorMessages.name).trim(),
  lastName: z.string().max(50, errorMessages.name).trim().optional(),
  email: z.string().email(errorMessages.email).trim().toLowerCase(),
  password: z.string().min(8, errorMessages.password.minLength).regex(passwordPatterns.uppercase, errorMessages.password.uppercase).regex(passwordPatterns.lowercase, errorMessages.password.lowercase).regex(passwordPatterns.number, errorMessages.password.number).regex(passwordPatterns.special, errorMessages.password.special)
});
const createOrganizationSchema = z.object({
  orgName: z.string().min(2, errorMessages.orgName).max(100, errorMessages.orgName).trim()
});
const joinOrganizationSchema = z.object({
  inviteToken: z.string().min(1, "Invite token is required").trim()
});
const loginSchema = z.object({
  email: z.string().email(errorMessages.email).trim().toLowerCase(),
  password: z.string().min(1, "Password is required")
});
z.object({
  name: z.string().min(1, "Site name is required").max(100, "Site name must be less than 100 characters").trim()
});
z.object({
  name: z.string().min(1, "Room name is required").max(100, "Room name must be less than 100 characters").trim(),
  building: z.string().max(10, "Building identifier must be less than 10 characters").trim().optional(),
  floor: z.number().int().min(0, "Floor must be 0 or higher").max(100, "Floor must be less than 100").optional()
});
z.object({
  name: z.string().min(1, "Asset name is required").max(100, "Asset name must be less than 100 characters").trim(),
  roomId: z.string().min(1, "Please select a room").cuid("Invalid room selection")
});
z.object({
  title: z.string().min(1, "Title is required").max(200, "Title must be less than 200 characters").trim(),
  description: z.string().min(1, "Description is required").max(2e3, "Description must be less than 2000 characters").trim(),
  assetId: z.string().min(1, "Please select an asset").cuid("Invalid asset selection").optional(),
  assignedToId: z.string().cuid("Invalid user selection").optional(),
  revisitSchedule: z.string().datetime("Invalid date format").optional()
});
z.object({
  email: z.string().email(errorMessages.email).trim().toLowerCase(),
  passphrase: z.string().min(1, "Passphrase is required").trim()
});
const passwordResetSchema = z.object({
  token: z.string().min(1, "Reset token is required").trim(),
  password: z.string().min(8, errorMessages.password.minLength).regex(passwordPatterns.uppercase, errorMessages.password.uppercase).regex(passwordPatterns.lowercase, errorMessages.password.lowercase).regex(passwordPatterns.number, errorMessages.password.number).regex(passwordPatterns.special, errorMessages.password.special),
  confirmPassword: z.string().min(1, "Please confirm your password")
}).refine((data) => data.password === data.confirmPassword, {
  message: "Passwords don't match",
  path: ["confirmPassword"]
});
z.object({
  firstName: z.string().min(1, errorMessages.name).max(50, errorMessages.name).trim().optional(),
  lastName: z.string().max(50, errorMessages.name).trim().optional(),
  email: z.string().email(errorMessages.email).trim().toLowerCase().optional(),
  phoneNumber: z.string().regex(/^[+]?[\d\s\-\(\)]+$/, "Please enter a valid phone number").optional()
});
function validateInput(schema, data) {
  const result = schema.safeParse(data);
  if (!result.success) {
    const errors = {};
    result.error.issues.forEach((issue) => {
      const key = issue.path[0];
      if (typeof key === "string") {
        errors[key] = issue.message;
      }
    });
    return { success: false, errors };
  }
  return { success: true, data: result.data };
}
export {
  createOrganizationSchema as c,
  joinOrganizationSchema as j,
  loginSchema as l,
  passwordResetSchema as p,
  registerSchema as r,
  validateInput as v
};
