import { r as redirect, f as fail } from "../../../chunks/index.js";
import { v as validateInput, c as createOrganizationSchema } from "../../../chunks/validation.js";
import { g as getPrisma } from "../../../chunks/prisma.js";
const load = async ({ locals }) => {
  if (!locals.user) {
    throw redirect(303, "/auth/login");
  }
  if (locals.authState === "org_member") {
    throw redirect(303, "/dashboard");
  }
  return {};
};
const actions = {
  default: async ({ request, locals }) => {
    if (!locals.user) {
      throw redirect(303, "/auth/login");
    }
    if (locals.authState === "org_member") {
      throw redirect(303, "/dashboard");
    }
    const formData = await request.formData();
    const isCreate = formData.get("create") === "true";
    if (isCreate) {
      const validation = validateInput(createOrganizationSchema, {
        orgName: formData.get("orgName")
      });
      if (!validation.success) {
        const firstError = Object.values(validation.errors)[0];
        return fail(400, {
          error: firstError,
          orgName: formData.get("orgName")
        });
      }
      const client = await getPrisma();
      const existingOrg = await client.organization.findUnique({
        where: { name: validation.data.orgName }
      });
      if (existingOrg) {
        return fail(400, {
          error: "An organization with this name already exists",
          orgName: formData.get("orgName")
        });
      }
      try {
        await client.$transaction(async (tx) => {
          const org = await tx.organization.create({
            data: { name: validation.data.orgName }
          });
          await tx.user.update({
            where: { id: locals.user.id },
            data: {
              organizationId: org.id,
              role: "ADMIN"
            }
          });
        });
        throw redirect(303, "/dashboard");
      } catch (error) {
        console.error("[ONBOARDING] Error creating organization:", error);
        return fail(500, {
          error: "Failed to create organization. Please try again.",
          orgName: formData.get("orgName")
        });
      }
    }
  }
};
export {
  actions,
  load
};
